.header {
  border: 1px solid #000;
  justify-content: space-between;
  display: flex;
}

.header ul {
  display: flex;
}

.header ul li {
  margin: 20px;
  list-style-type: none;
}

.header img {
  width: 150px;
}

.body button {
  margin: 10px;
}

.res-container {
  flex-wrap: wrap;
  display: flex;
}

.res-card {
  background: #f0f0f0;
  width: 200px;
  margin: 5px;
  padding: 5px;
}

.res-card:hover {
  cursor: pointer;
  border: 1px solid #000;
}

.res-card img {
  width: 190px;
}

.res-card h4 {
  margin: 2px;
}

.res-card h3 {
  margin: 4px;
}
